import React from 'react';

type MainButtonProps = {
  children: React.ReactNode;
  type?: 'button' | 'submit' | 'reset';
  onClick?: () => void;
  className?: string;
  disabled?: boolean;
  isLoading?: boolean;
};

const MainButton = ({
  children,
  type = 'button',
  onClick,
  className,
  disabled,
  isLoading,
}: MainButtonProps) => {
  return (
    <button
      className={`btn btn--main ${className}`}
      type={type}
      onClick={onClick}
      disabled={disabled || isLoading}
    >
      {isLoading ? <div className='loader'></div> : children}
    </button>
  );
};

export default MainButton;
